<template>
  <div class="gradualBg">
    <NavHeader />
    <!--  -->
    <div class="row justify-content-center" :style="homeBg">
      <div class=" col-md-8 col-12 row">
        <div class="mapCube px-0">
          <l-map ref="myMap" :zoom="zoom" :center="localCenter" :options="mapOptions">
            <l-tile-layer :url="urlLocal" :attribution="localAttribution" />
            <l-marker 
            :lat-lng = "[stop.lat,stop.lon]" 
            v-for="(stop, index) in nearStopData" :key="index">
              <l-icon
                :icon-url="icon.black"
                :shadow-url="icon.shadowUrl"
                :icon-size="icon.iconSize"
                :icon-anchor="icon.iconAnchor"
                :popup-anchor="icon.popupAnchor"
                :shadow-size="icon.shadowSize"
              />
              <l-popup>
                <div style="font-size: 14px" class="badgeBusStopInfo">
                {{ stop.stopname }}
              </div>
              <span class="badgeBusStopInfo">行駛路線:{{ stop.RouteNameNo }}</span>
              </l-popup>
            </l-marker>
          </l-map>
        </div>
      </div>
      <div class="col-md-4 col-12 row px-3 nearbuscardHeight">
        <div class="card mt-2 nearbuscard" v-for="(stop,index) in nearStopData" :key="index">
          <div class="card-body row ">
            <div class="col-6">站名:{{stop.stopname}}</div>
            <div class="col-6">路線:{{stop.RouteNameNo}}</div>
            <div class="col-6 mt-1"></div>
            <div class="col-6 mt-1">{{stop.EstimateTime}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import NavHeader from "@/components/NavHeader.vue";
import jsSHA from "jssha";
export default {
  name: "rideplan",
  components: {
    NavHeader,
  },
  data() {
    return {
      homeBg: {
        width: "100vw",
        height: "100vh",
        zIndex: "2",
        backgroundSize: "cover",
        backgroundImage: `url(${require("@/assets/busImg/background.png")})`,
        // backgroundRepeat: "no-repeat",
      },
      busImgHalf: {
        backgroundImage: `url(${require("@/assets/busImg/bus.png")})`,
        backgroundRepeat: "no-repeat",
        width: "100%",
        height: "100%",
      },
      zoom:'16',
      localCenter:[25.0375417, 121.5644327],
      mapOptions: {
        zoomControl: false,
      },
      localAttribution:
        '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      urlLocal: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      icon: {
        iconSize: [25, 34.5],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41],
        shadowUrl:
          "https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png",
        black: `${require("@/assets/busImg/BusSign.png")}`,
      },
      searchDistance:'500',
      nearStopData:[],
    };
  },
  methods: {
    getAuthorizationHeader() {
      const AppID = "71661ab7d83947fd9e8553690d6cc7bc";
      const AppKey = "5Wd31rSWRfudLXs4hoL7jdfTs5U";
      // // id，key過兩天移除
      const GMTString = new Date().toGMTString();
      let ShaObj = new jsSHA("SHA-1", "TEXT");
      ShaObj.setHMACKey(AppKey, "TEXT");
      ShaObj.update("x-date: " + GMTString);
      const HMAC = ShaObj.getHMAC("B64");
      const Authorization = `hmac username="${AppID}", algorithm="hmac-sha1", headers="x-date", signature="${HMAC}"`;
      return {
        Authorization: Authorization,
        "X-Date": GMTString,
      };
    },
    nearBusStopFn(){
      // const vm =this
      this.axios({
        method:'get',
        headers:this.getAuthorizationHeader(),
        url:`https://ptx.transportdata.tw/MOTC/v2/Bus/Stop/NearBy?$spatialFilter=nearby(${this.localCenter[0]}%2C%20${this.localCenter[1]}%2C%20${this.searchDistance})`,
      }).then((response)=>{
        console.log(response.data);
        const nearStopTempData = response.data;
        for(let i=0;nearStopTempData.length>i;i++){
          this.nearStopData.push({
            lat:nearStopTempData[i].StopPosition.PositionLat,
            lon:nearStopTempData[i].StopPosition.PositionLon,
            stopname:nearStopTempData[i].StopName.Zh_tw,
            stopUID:nearStopTempData[i].StopUID,
          })
        }
        
        this.nearBusArrivalFn()
      })
      
    },
    nearBusArrivalFn(){
      this.axios({
        method:'get',
        header:this.getAuthorizationHeader(),
        url:`https://ptx.transportdata.tw/MOTC/v2/Bus/EstimatedTimeOfArrival/NearBy?%24top=30&%24spatialFilter=nearby(${this.localCenter[0]}%2C%20${this.localCenter[1]}%2C%20${this.searchDistance})`,
      }).then((response)=>{
        console.log(response.data)
        const data = response.data
        for(let i=0;data.length>i;i++){
          for(let j=0;this.nearStopData.length>j;j++){
            
              if(data[i].StopUID === this.nearStopData[j].stopUID){
                let waitTime;
                if(!data[i].EstimateTime){
                  waitTime = '未發車'
                }else{
                  waitTime = Math.ceil(data[i].EstimateTime/60);
                  this.nearStopData[j]['Direction']=data[i].Direction;
                };
                if(waitTime === 1){
                  waitTime = 'Coming soon'
                }
                this.nearStopData[j]['RouteNameNo'] = data[i].RouteName.Zh_tw;
                this.nearStopData[j]['EstimateTime'] = waitTime + '分';
                
              }
            
          }
        }
        console.log(this.nearStopData)
      })
    },
    stopInfoFn(stop){
      console.log(stop)
    }
  },
  mounted() {
    const vm = this;
    return new Promise((resolve)=>{
      resolve();
      vm.$nextTick(async()=>{
      await navigator.geolocation.getCurrentPosition((position) => {
        const p = position.coords;
        vm.localCenter = [p.latitude, p.longitude];
        // 將目前的位置的標記點彈跳視窗打開
        console.log(vm.localCenter)
        // this.$refs.location.mapObject.openPopup();
        })
      });
    }).then(()=>{
      setTimeout(() => {
        vm.nearBusStopFn();
      }, 100);
      setInterval(vm.nearBusStopFn,60000)
    });
    
  },
};
</script>
<style lang="scss">
@import "@/assets/scss/all.scss";
@import "@/assets/scss/citybus.scss";
.nearbuscard{
  background: #161933 81%;
  opacity:0.6;
  color: white;
  border:2px solid #c0a7c4;
  
}
.nearbuscardHeight{
  height: calc(100vh - 60px);
overflow-y: auto;
}
</style>

